<script setup>
import { ref } from 'vue';

const selectedDefinition = ref('');
const selectedTitle = ref('');

const definitions = {
  "Floodlight": `These systems offer broad, continuous coverage over large geographic areas. Ideal for scanning wide areas and detecting changes or patterns. Limited resolution but high persistence. Often used to cue more detailed surveillance.`,
  "Searchlight": `Manned platforms like P-8A Poseidon or E-7A Wedgetail act as mid-range ISR platforms. They balance resolution and coverage and are often tasked to narrow down potential areas of interest for follow-up.`,
  "Spotlight": `Spotlight platforms offer high-resolution, focused surveillance over smaller areas. UAVs, special forces teams, or EO/IR pods provide critical detail on a target once it’s been cued by higher-level sensors.`,
  "Scan": `The scanning process conducted by floodlight sensors establishes situational context and detects initial indicators that something is happening. Forms the first step in the cueing chain.`,
  "Cue": `Cueing is the act of tasking another ISR platform to focus on a potential target or area of interest identified by a broader sensor. Critical for time-sensitive ISR.`,
  "Focus": `The final step in the ISR engagement sequence. A detailed sensor (e.g. UAV or special mission aircraft) confirms, investigates, or collects precise data on a target. Enables actionable intelligence or targeting decisions.`
};

const showDefinition = (id) => {
  selectedDefinition.value = definitions[id] || 'Definition not found.';
  selectedTitle.value = id.replace(/([A-Z])/g, ' $1').trim();
};
</script>

